header{
    height: 140vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}


/*======================CTA===================*/

.cta {
margin-top: 2.5rem;
display: flex;
gap: 1.2rem;
justify-content: center;
}

/*======================HEADER SOCIALS===================*/
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 5rem;
}

.header_socials::after{
    content: '';
    width: 1px;
    height:2rem; 
    background: var(--color-primary);
}


/*====================== me ===================*/

.me{
    background-color: linear-gradient(var(--color-primary), transparent);    width: 22rem;
    height: 30rem;
    position: absolute;
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    left: calc(50% - 11rem);
    overflow: hidden;
    padding: 5rem 1.5rem 1.5 1.5;
}

/*====================== Scroll Down ===================*/

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 7rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}

/* ======================== MEDIA QUERIES (MEDIUM DEVICES========================*/

@media screen and (max-width: 1024px) {
   header{
   height: 70vh;
}
    
  }

  /* ======================== MEDIA QUERIES (SMALL DEVICES========================*/

@media screen and (max-width: 600px) {
   header{
    height: 100vh;;
   }
   .header_socials, .scroll_down {
    display: none;
   }
    
  }